import React, { Component } from 'react';
import { Modal } from 'react-bootstrap';
class ShareEmbedModal extends Component {
    constructor(props) {
        super(props);

    }
    state = {
        emailId: ""
    };

    handleClose = event => this.props.shareEmbedCallback(false)

    onChangeEmail = event => this.setState({ emailId: event.target.value })

    render() {
        const { shareEmbedModal } = this.props
        const { emailId } = this.state
        return <Modal show={shareEmbedModal} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Send code by email</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <p className="text-center mb-4">Share this embed code with your team. Enter the email Id to send the code directly to them</p>
                    <div className="mb-3 mb-xl-4">
                        <textarea className="form-control" placeholder="Ex: support@taggbox.com,info@taggbox.com" value={emailId} onChange={this.onChangeEmail}></textarea>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className="btn btn-primary btn-sm btn-min-w" onClick={this.props.onClickToShare(this.state.emailId)}>Share Code</button>
                </div>
            </Modal.Footer>
        </Modal>
    }
}

export default ShareEmbedModal;
